.feedback-item {
  width: 100%;
  background-color: $bgc;
  margin: 40px 0;
  padding: 18px 32px;
  display: flex;

  &__composition {
    width: 55%;
    padding-top: 1.47em; }
  &__title {
    display: flex;
    justify-content: space-between;
    font-weight: 700;
    font-size: 1.47em;
    color: #000; }


  &__cta {
    //width: 21.8%
    padding: {
      top: 24px;
      right: 3.89%;
      bottom: 31px; }
    text-align: center;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }

  &__btns {
    display: flex;
    justify-content: space-around; }
  &__btn {}


  &__img {
    width: 23.2%;
    min-width: 110px;
    display: flex;
    align-items: center; } }

@media only screen and (max-width : 1560px) {
	.feedback-item {
    &__cta {
      margin: 0 auto;
      padding-right: 0; } } }

@media only screen and (max-width : 1200px) {
	/**/ }
@media only screen and (max-width : 768px) {
  .feedback-item {
    flex-wrap: wrap;
    justify-content: space-between;
    padding: {
      left: 22px;
      right: 22px; }

    &__composition {
      width: 100%; }
    &__cta {
      margin-left: 0;
      padding: 0;
      justify-content: space-around; } } }

@media only screen and (max-width : 576px) {
  .feedback-item {} }



@media only screen and (max-width : 480px) {
  /**/ }

